/* eslint-disable jsx-a11y/media-has-caption */
import React, { useEffect } from "react";
import styled from "styled-components";

const StyledDiv = styled.div`
  display: block;
  align-items: center;
`;

const StyledDivContainer = styled.div`
  display: block;
  // max-width: 128px;
  // max-height: 96px;
`;

const StyledVid = styled.video`
  // display: block;
  // align-items: center;
`;

const StyledButton = styled.button`
  // display: block;
  // align-items: center;
`;

const Camera = () => {
  useEffect(() => {
    const getStream = async () => {
      let vidStream;
      // Get access to the camera!
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        try {
          vidStream = await navigator.mediaDevices.getUserMedia({ video: true });
          document.getElementById("video").srcObject = vidStream;
          document.getElementById("video").play();
        } catch (e) {
          console.log(e);
        }
      }
      return vidStream;
    };

    const streamPromise = getStream();

    return async () => {
      let stream;
      try {
        stream = await streamPromise;
        stream.getTracks().forEach(track => {
          track.stop();
        });
      } catch (e) {
        console.log(e);
      }
    };
  }, []);

  const deriveGallery = ({ width, height, maxNum = 1 }) => {
    const numberOfSnaps = width % maxNum === 0 ? maxNum : maxNum - 1;
    const onSnap = () => {
      const drawImg = id => {
        document
          .getElementById(`canvas-${id}`)
          .getContext("2d")
          .drawImage(document.getElementById("video"), 0, 0, width / maxNum, 48);
      };
      let count = 0;
      window.setInterval(() => {
        if (count === numberOfSnaps) {
          window.clearInterval();
          return;
        }
        console.log(count);
        drawImg(count);
        count += 1;
      }, 200);
    };
    const canvasArray = () => {
      const arr = [];
      for (let x = 0; x < numberOfSnaps; x += 1) {
        arr.push(<canvas id={`canvas-${x}`} width={width / maxNum} height="48" />);
      }
      return arr;
    };

    return {
      canvasArray: canvasArray(),
      onSnap
    };
  };

  const gallery = deriveGallery({ width: 512, maxNum: 16 });

  return (
    <StyledDiv>
      <StyledDivContainer>
        {gallery.canvasArray.map(canvas => {
          console.log(canvas);
          return canvas;
        })}
      </StyledDivContainer>
      <StyledVid data-testid="camera" id="video" width="128" height="96" autoPlay />
      <StyledDiv>
        <StyledButton id="snap" type="button" onClick={gallery.onSnap}>
          Snap!
        </StyledButton>
      </StyledDiv>
    </StyledDiv>
  );
};

export default Camera;
