import React from "react";
import styled from "styled-components";
import { string, arrayOf, shape, func } from "prop-types";

const StyledUl = styled.ul`
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
`;
const StyledButton = styled.button`
  background: none;
  border: none;
  margin: 1rem;
  height: 5rem;
  width: 15rem;
  font-size: 1.6rem;
  max-width: 10rem;
  font-weight: bold;
  text-decoration: none;
  display: block;
  align-items: flex-start;
  color: #333;
  &:hover {
    background: #ccc;
    color: purple;
    &:focus {
      color: red;
    }
  }
`;
const StyledSpan = styled.span`
  display: block;
  font-size: 1rem;
  font-weight: normal;
  color: #888;
  margin: 0.25rem 0 0 0;
  ${StyledButton}:hover & {
    background: white;
  }
`;

const Menu = ({ menuItemList }) => {
  return (
    <StyledUl>
      {menuItemList.map(item => (
        <li key={`${item.name}`}>
          <StyledButton onClick={() => item.onClick(item.name.toLowerCase())}>
            {item.name}
            <StyledSpan>{item.description}</StyledSpan>
          </StyledButton>
        </li>
      ))}
    </StyledUl>
  );
};

Menu.propTypes = {
  menuItemList: arrayOf(
    shape({
      name: string,
      description: string,
      onClick: func
    })
  )
};

Menu.defaultProps = {
  menuItemList: []
};

export default Menu;
