import React, { useState } from "react";
import styled from "styled-components";
import Geolocation from "./Geolocation";
import Menu from "./Menu";
import Camera from "./Camera";

const StyledNav = styled.nav`
  background: red;
`;
const StyledDiv = styled.div`
  background: grey;
  display: block;
`;

const Examples = () => {
  const [activeItem, setActiveItem] = useState("readme");
  const handleMenuItem = name => {
    setActiveItem(name);
  };

  const menuItemList = [
    { onClick: handleMenuItem, name: "Readme", description: "Read this for browser support" },
    { onClick: handleMenuItem, name: "Geolocation", description: "Pinpoint your current location" },
    { onClick: handleMenuItem, name: "Camera", description: "Say Cheese! __________" },
    { onClick: handleMenuItem, name: "Notifications", description: "Triggers a push notification" }
  ];

  return (
    <div data-testid="examples" className="examples">
      <StyledDiv>
        {activeItem === "readme" && "readme"}
        {activeItem === "geolocation" && (
          <div>
            geolocation
            <Geolocation />
          </div>
        )}
        {activeItem === "camera" && (
          <div>
            camera
            <Camera />
          </div>
        )}
        {activeItem === "notifications" && "notifications"}
      </StyledDiv>
      <StyledNav>
        <Menu menuItemList={menuItemList} />
      </StyledNav>
    </div>
  );
};

export default Examples;
