import React, { useState } from "react";
import { Map, Marker, Popup, TileLayer } from "react-leaflet";
import L from "leaflet";
import iconRetinaUrl from "leaflet/dist/images/marker-icon-2x.png";
import iconUrl from "leaflet/dist/images/marker-icon.png";
import shadowUrl from "leaflet/dist/images/marker-shadow.png";

// https://github.com/PaulLeCam/react-leaflet/issues/453
// eslint-disable-next-line no-underscore-dangle
delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl,
  iconUrl,
  shadowUrl
});

const Geolocation = () => {
  const [state, setState] = useState({
    popUpText: "Latitude: 1.2796562, Longitude: 103.8164022",
    center: [1.2796562, 103.8164022]
  });

  const getLocation = () => {
    navigator.geolocation.getCurrentPosition(
      position => {
        setState({
          popUpText: `Latitude: ${position.coords.latitude}, Longitude: ${position.coords.longitude}`,
          center: [position.coords.latitude, position.coords.longitude]
        });
      },
      error => console.log(error.message),
      { enableHighAccuracy: true, timeout: 20000, maximumAge: 1000 }
    );
  };

  return (
    <>
      <button type="button" onClick={getLocation}>
        Get My Location
      </button>
      <div data-testid="map">
        <Map center={state.center} zoom={15} minZoom={15} maxZoom={15} zoomControl={false}>
          <TileLayer
            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          <Marker position={state.center}>
            <Popup>{state.popUpText}</Popup>
          </Marker>
        </Map>
      </div>
    </>
  );
};

export default Geolocation;
